import { mapState } from "vuex";

export default {
    data() {
        return {
            setting: {},
            list: [],
            more: false,
            page: 1
        }
    },
    computed: {
        ...mapState({
        })
    },
    methods: {
        getSetting() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.$request({
                url: that.$api.stock.setting,
            }).then(response=>{
                if(response.code == 0) {
                    that.setting = response.data;
                    that.getList();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.stock.bonus,
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    if(response.data.list.length == response.data.pagination.pageSize) {
                        this.more = true;
                        this.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getMore() {
            let that = this;
            that.more = false;
            uni.showLoading({
                mask: true,
                title: '加载更多...'
            });
            that.$request({
                url: that.$api.stock.bonus,
                data: {
                    page: that.page
                }
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = that.list.concat(response.data.list);
                    if(response.data.list.length == response.data.pagination.pageSize) {
                        that.more = true;
                        that.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        }
    },

    onLoad(options) { this.$commonLoad.onload(options);
        if(options.name) {
            uni.setNavigationBarTitle({
                title: options.name,
            })
        }
        this.getSetting();
    },
    onReachBottom() {
        if(this.more) {
            this.getMore();
        }
    }
}
